
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
html, body{
  overflow-x:scroll;
  width:100vw;
}
nav{
  width: 100vw;
  position:fixed;
  top:0;
  opacity: 0.9;
}   

